// SCROLL BAR //
::-webkit-scrollbar {
  width: 4px;
  height: 6px;
}
/* ::-webkit-scrollbar-button {
  background-color: var(--primary);
} */
::-webkit-scrollbar-thumb {
  background-color: var(--green-70);
  // border-radius: 10px;
  max-width: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: var(--green);
}
::-webkit-scrollbar-track {
  background-color: var(--background);
  padding: 2px;
  box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  // border-radius: 4px;
  position: static;
  // overflow-x: hidden;
}
html {
  overflow-x: hidden;
}

body {
  background-color: var(--background);
  color: var(--ecriture);
  margin: 0;
  font-family: $font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

%button {
  height: 40px;
  border: none;
  outline: none;
  background-color: var(--primary);
  color: white;
  font-size: 18px;
  cursor: pointer;
  font-weight: 300;
  font-size: 16px;
  padding: 0 10px;
}

%tooltip {
  position: absolute;
  left: 50%;
  top: 0px;
  font-size: 16px;
  font-family: $font;
  font-weight: normal;
  text-transform: lowercase;
  transform: translate(-50%, -50%);
  border-radius: 6px;
  min-width: fit-content;
  height: fit-content;
  width: fit-content;
  padding: 5px 10px;
  text-align: center;
  background-color: var(--backgroundWhite);
  color: var(--ecriture);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  transition: 0s;
  opacity: 0;
  pointer-events: none;
}

%link {
  ul {
    margin: 0;
    padding: 0;
  }
  ul li {
    margin: 0 0 20px 0;
    padding: 0;
    list-style: none;
    font-size: 18px;
    font-weight: 500;
  }
  ul li a,
  ul li span {
    color: rgba(0, 0, 0, 0.7);
    text-decoration: none;
    position: relative;
    padding: 10px 0;
    font-family: "Poppins", Arial, sans-serif;
    transition: all 0.3s ease;
  }
  ul li a:after,
  ul li span:after {
    content: "";
    position: absolute;
    height: 2px;
    bottom: 7px;
    left: 0;
    right: 0;
    background-color: var(--primary);
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  ul li a:hover,
  ul li span:hover {
    text-decoration: none;
    color: var(--primary);
  }
  ul li a:hover:after,
  ul li span:hover:after {
    visibility: visible;
    transform: scaleX(1);
  }
  ul li a.active,
  ul li span.active {
    font-weight: 600;
    color: var(--primary);
  }
  ul li a.active a,
  ul li span.active span {
    color: var(--primary);
  }
  ul li a.active a:after,
  ul li span.active span:after {
    visibility: visible;
    transform: scaleX(1);
  }
}

%center {
  display: flex;
  justify-content: center;
  align-items: center;
}

%search_group {
  position: relative;
  input {
    height: 20px;
    @include input();
  }
  button {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 50px;
    color: var(--primary);
    background-color: transparent;
    @extend %button;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes zoomInY {
  0% {
    opacity: 0;
    transform: scaleY(0.9);
  }
  100% {
    opacity: 1;
    transform: scaleY(1);
  }
}
@keyframes zoomInX {
  0% {
    opacity: 0;
    transform: scaleX(0.9);
  }
  100% {
    opacity: 1;
    transform: scaleX(1);
  }
}
@keyframes zoomIn {
  0% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes move {
  0% {
    top: 35px;
    right: 48px;
  }
  50% {
    top: 0px;
    right: 16px;
  }
  100% {
    top: 25px;
    right: 16px;
  }
}
