  --red: #c7192e;
  --blue: #247ba0;
  --yellow: #ffc700;
  --orange: #ff9900;
  --green: #22a77d;

  --red-70: #c7192e70;
  --blue-70: #247ba070;
  --yellow-70: #ffc70070;
  --orange-70: #ff990070;
  --green-70: #22a77d70;

  --color1: #50514f;

  --background: #f8f9fa;
  --backgroundWhite: #ffffff;
  --writing: #131523;
  --dark: #131523;
}
